import React from "react";
import { NavLink } from "react-router-dom";

function Common(props){
  return (
      <>
      <section id="header" className="d-flex align-items-center">
      <div className="container-fluid">
      <div className="row">
        <div className="col-10 mx-auto">
        <div className="row">

        
            <div className="col-md-6 pt-6 pt-lg-0 order-3 order-lg-1 d-flex justify-content-center flex-column" >
             <h1>
                 {props.name} <strong className="brand-name"> Specialist</strong>
             </h1>
             <h4 className="my-3">
                    {props.detail}
             </h4>
              <div className="mb-5">
               <NavLink to={props.visit} className="btn-get-started"> {props.btname} </NavLink>
              </div>
            </div>
            <div className="col-lg-6 order-1 order-lg-2 header-img mt-5">
            <img src={props.imgsrc} className="img-fluid animated" alt="img"/>

            </div>
        </div>
      </div>
      </div>
      </div> 

      </section>
      </>
  );
};
export default Common;